/*
0 - 600px:  Phone
600px - 900px:  Tablet portrait
900px - 1200px:  Tablet landscape
[1200 - 1800]: is where our normal style applies
1800px + :  Big desktop
*/
/*
$breakpoint argument choices:
- phone
- tablet portrait
- tablet landscape
- big desktop    
*/
@keyframes borderGrowFromLeft {
  0% {
    width: 0; }
  50% {
    width: 100%; } }

.portifolio {
  min-height: 100vh;
  height: 100%;
  width: 100vw;
  display: flex; }
  @media (max-width: 37.5em) {
    .portifolio {
      flex-direction: column; } }
  .portifolio__user-info {
    background-color: #333;
    flex: 0 0 35%; }
  .portifolio__user-story {
    padding: 75px 60px 60px 60px;
    background-color: #fff;
    flex: 1; }
  .portifolio .user-presentation {
    animation: opacityAnimation 0.8s ease;
    position: relative;
    display: flex;
    flex-direction: column;
    align-items: center;
    background-color: transparent;
    margin: 0 auto;
    margin-top: 1.2rem;
    padding: 3rem;
    /* The typing effect */
    /* The typewriter cursor effect */ }
    .portifolio .user-presentation__photo-container {
      margin-top: 1rem;
      margin-bottom: 3rem;
      width: 200px;
      height: 200px;
      overflow: hidden;
      transform: rotate(45deg);
      border: 1px solid #fcfcfc;
      border-spacing: -30px;
      width: 160px;
      height: 160px; }
    .portifolio .user-presentation__photo-container::before {
      content: "";
      width: 210px;
      height: 210px;
      margin: -26px;
      position: absolute;
      background-image: url("../../assets/img/avatar-dev.png");
      background-repeat: no-repeat;
      background-size: cover;
      transform: rotate(-45deg);
      clip-path: polygon(50% 0%, 100% 50%, 50% 100%, 0% 50%); }
    .portifolio .user-presentation__name {
      text-transform: uppercase;
      color: #fefefe;
      font-size: 4rem;
      letter-spacing: 0.6rem;
      font-weight: 600;
      word-spacing: 100vw;
      text-align: center; }
    .portifolio .user-presentation__profession {
      position: relative;
      text-transform: uppercase;
      color: #d4d4d4;
      font-size: 1rem;
      font-weight: 100;
      overflow: hidden;
      /* Ensures the content is not revealed until the animation */
      border-right: 0.1em solid #fff;
      /* The typwriter cursor */
      white-space: nowrap;
      /* Keeps the content on a single line */
      margin: 1.2rem auto;
      /* Gives that scrolling effect as the typing happens */
      letter-spacing: 0.2em;
      /* Adjust as needed */
      animation: typing 2.5s steps(40, end), blink-caret 0.75s step-end infinite; }

@keyframes typing {
  from {
    width: 0; }
  to {
    width: 40%; } }
    @media screen and (max-width: 448px) {
      .portifolio .user-presentation {
        /* The typing effect */ }
      @keyframes typing {
        from {
          width: 0; }
        to {
          width: 33%; } }
        .portifolio .user-presentation__profession {
          animation: typing 1.5s steps(40, end), blink-caret 0.75s step-end infinite; } }
    @media screen and (min-width: 448px) and (max-width: 600px) {
      .portifolio .user-presentation {
        /* The typing effect */ }
      @keyframes typing {
        from {
          width: 0; }
        to {
          width: 20%; } }
        .portifolio .user-presentation__profession {
          animation: typing 1.5s steps(40, end), blink-caret 0.75s step-end infinite; } }

@keyframes blink-caret {
  from,
  to {
    border-color: transparent; }
  50% {
    border-color: #fff; } }
  .portifolio .line-separation {
    margin-left: 1.2rem;
    height: 0.2rem;
    color: #fcfcfc;
    background-color: #fcfcfc;
    border: none; }

@keyframes opacityAnimation {
  0% {
    opacity: 0; }
  100% {
    opacity: 1; } }
